@charset "UTF-8";
@import url(../../dist/css/normalize.css);
/* Reset */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  font-family: inherit;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

@font-face {
  font-family: "Sora-Regular";
  src: url("../../src/assets/fonts/Sora-Regular.ttf");
}
@font-face {
  font-family: "Sora-Bold";
  src: url("../../src/assets/fonts/Sora-Bold.ttf");
}
@font-face {
  font-family: "Sora-ExtraBold";
  src: url("../../src/assets/fonts/Sora-ExtraBold.ttf");
  font-weight: 800;
}
@font-face {
  font-family: "Sora-SemiBold";
  src: url("../../src/assets/fonts/Sora-SemiBold.ttf");
}
body {
  font-family: "Sora-Regular", sans-serif;
  font-size: 16px;
  color: #71717A;
}

.header {
  padding: 24px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .header__logo .logo {
  perspective: 1000px;
  width: 140px;
  height: 20px;
}
.header .header__logo .logo-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transform-style: preserve-3d;
  animation: flip 10s infinite; /* Автоматическая анимация (3 секунды) */
}
.header .header__logo .logo-front, .header .header__logo .logo-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .header__logo .logo-back {
  transform: rotateY(180deg);
}
.header .header__logo .logo .logo-img {
  max-width: 100%;
  max-height: 100%;
}
@keyframes flip {
  0% {
    transform: rotateY(0deg);
  }
  /* Начало (лицевая сторона) */
  10% {
    transform: rotateY(180deg);
  }
  /* Быстрый переворот (10% от 3 сек = 0.3 сек) */
  50% {
    transform: rotateY(180deg);
  }
  /* Обратная сторона видна до 50% (1.5 сек) */
  60% {
    transform: rotateY(360deg);
  }
  /* Быстрый возврат (ещё 0.3 сек) */
  100% {
    transform: rotateY(360deg);
  }
  /* Лицевая сторона видна до конца (3 сек) */
}
.header .header__nav .header__nav-list {
  display: flex;
}
.header .header__nav .header__nav-list .header__nav-item {
  margin: 0 20px;
}
.header .header__nav .header__nav-list .header__nav-item .header__nav-item-link {
  font-family: "Sora-SemiBold", sans-serif;
  font-size: 20px;
  color: #000000;
}
.header .header__button {
  width: 153px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 16px 0;
  gap: 8px;
  background: #000000;
  border-radius: 4px;
}
.header .header__button .header__button-button {
  font-family: "Sora-SemiBold", sans-serif;
  font-size: 20px;
  color: #ffffff;
}

.hero {
  padding: 60px 0;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.hero .hero__content {
  margin-top: 100px;
  max-width: 600px;
}
.hero .hero__content .hero__content-greeting {
  font-family: "Sora-Regular", sans-serif;
  font-size: 20px;
  color: #000000;
  margin-bottom: 20px;
  line-height: 32px;
}
.hero .hero__content .hero__content-title {
  font-family: "Sora-ExtraBold", sans-serif;
  font-size: 48px;
  color: #000000;
  margin-bottom: 20px;
  font-weight: 800;
}
.hero .hero__content .hero__content-description {
  margin-top: 108px;
  line-height: 24px;
}
.hero .hero__content .social_icon {
  margin-top: 64px;
}
.hero .hero__content .social_icon .icon {
  margin-right: 24px;
}
.hero .hero__banner {
  position: absolute;
  top: 60px;
  right: 0;
}

.block_flip-card {
  display: flex;
  justify-content: center;
}

.flip-card {
  perspective: 1000px;
  width: 186px;
  height: 186px;
  margin-left: 30px;
}
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}
.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
  cursor: pointer;
}
.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.flip-card-back {
  transform: rotateY(180deg);
}
.flip-card img {
  max-width: 100%;
  max-height: 100%;
}

@media screen and (max-width: 1320px) {
  .container {
    margin: 0 20px;
  }
}
/* Медиа-запрос для мобильных устройств */